let res=parseInt(Math.abs(now - date) / 1000 / 60 / 60 / 24)
return res;
},
date3:()=>{
let date=new Date('2018-06-01');
let now=new Date();
let res=parseInt(Math.abs(now - date) / 1000 / 60 / 60 / 24)
return res;
}

}
}



